import { GET_ALL_SCHEMAS, SELECT_POSITION } from "../actionTypes";

export const getAllSchemas = () => (dispatch) => {
  const allSchemas = JSON.parse(localStorage.getItem("schemas")) || {};

  dispatch({
    type: GET_ALL_SCHEMAS,
    payload: Object.keys(allSchemas),
  });
};

export const selectPosition = (name) => (dispatch) => {
  dispatch({
    type: SELECT_POSITION,
    payload: name,
  });
};
