import {
  SET_INITIAL_PARAMS,
  DRAW_COLOR,
  ERASE_COLOR,
  SAVE_SCHEMA,
  LOAD_SCHEMA,
  CHECK_CURRENT_COLOR,
  CLEAR_PARAMS,
} from "../actionTypes";

const initialState = {
  cells: [],
  name: null,
  width: null,
  height: null,
  colors: [],
  currentColor: "",
};

const schemaReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case SET_INITIAL_PARAMS:
      return {
        ...state,
        width: payload.width,
        height: payload.height,
        name: payload.name,
        colors: payload.colorsAsset,
      };
    case DRAW_COLOR:
      return {
        ...state,
        cells: {
          ...state.cells,
          [payload]: state.currentColor,
        },
      };
    case ERASE_COLOR:
      const cells = {
        ...state.cells,
      };

      delete cells[payload];
      return {
        ...state,
        cells,
      };
    case SAVE_SCHEMA:
      return state;
    case LOAD_SCHEMA:
      return {
        ...state,
        cells: payload.cells,
        name: payload.name,
        width: payload.width,
        height: payload.height,
        colors: payload.colors,
      };
    case CHECK_CURRENT_COLOR:
      return {
        ...state,
        currentColor: "#" + payload,
      };
    case CLEAR_PARAMS:
      return {
        ...state,
        width: null,
        height: null,
        cells: [],
        colors: [],
        name: null,
        currentColor: null,
      };
    default:
      return state;
  }
};

export default schemaReducer;
