import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { withRouter } from "react-router-dom";

import LoadSchema from "./LoadSchema";

import { getAllSchemas } from "../../../actions/service-actions";
import {
  loadSchema,
  renameSchema,
  deleteSchema,
} from "../../../actions/schema-action";

class LoadSchemaContainer extends Component {
  constructor(props) {
    super(props);

    const { selectedSchema, getAllSchemas } = this.props;

    getAllSchemas();

    this.state = {
      rename: false,
      name: {
        value: selectedSchema,
        note: "",
      },
    };

    this.handleLoad = this.handleLoad.bind(this);
    this.handleRename = this.handleRename.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
  }

  handleLoad() {
    const { selectedSchema, history } = this.props;
    if (!selectedSchema) return;
    history.push(`/editor/${selectedSchema}`);
  }

  handleRename(name) {
    const { selectedSchema } = this.props;
    if (!selectedSchema) return;
    this.setState({
      rename: true,
      name: {
        value: selectedSchema,
      },
    });
  }

  handleChange(e) {
    this.setState({
      name: {
        value: e.target.value,
        note: "",
      },
    });
  }

  handleCancel() {
    this.setState({
      rename: false,
      name: {
        value: "",
        note: "",
      },
    });
  }

  handleSubmit() {
    const { name } = this.state;
    const { selectedSchema, renameSchema } = this.props;

    let ready = true;

    if (!name.value.length) {
      this.setState({
        name: {
          value: name.value,
          note: "Requirement field",
        },
      });
      ready = false;
    } else if (name.value.length <= 1) {
      this.setState({
        name: {
          value: name.value,
          note: "Incorrect format",
        },
      });
      ready = false;
    } else if (name.value.length > 20) {
      this.setState({
        name: {
          value: name.value,
          note: "Incorrect format",
        },
      });
      ready = false;
    } else if (!name.value.match(/^[a-zA-Z][a-zA-Z0-9-_.]/)) {
      this.setState({
        name: {
          value: name.value,
          note: "Incorrect format",
        },
      });
      ready = false;
    }

    if (!ready) return;

    this.setState({
      rename: null,
    });

    renameSchema(selectedSchema, name.value);
  }

  handleDelete() {
    const { selectedSchema, deleteSchema } = this.props;
    deleteSchema(selectedSchema);
  }

  render() {
    const { allSchemas, selectedSchema } = this.props;
    const { rename, name } = this.state;
    return (
      <LoadSchema
        allSchemas={allSchemas}
        selectedSchema={selectedSchema}
        handleLoad={this.handleLoad}
        handleRename={this.handleRename}
        handleChange={this.handleChange}
        handleCancel={this.handleCancel}
        handleSubmit={this.handleSubmit}
        handleDelete={this.handleDelete}
        rename={rename}
        name={name}
      />
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    allSchemas: state.service.allSchemas,
    selectedSchema: state.service.selectedSchema,
    history: ownProps.history,
  };
};

const mapDispatchToProps = {
  getAllSchemas,
  loadSchema,
  renameSchema,
  deleteSchema,
};

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(LoadSchemaContainer);
