import React from "react";
import { Route, Redirect } from "react-router-dom";
import InitialPageContainer from "./components/InitialPage/InitialPageContainer";
import EditorPageContainer from "./components/EditiorPage/EditorPageContainer";

const App = () => {
  return (
    <div>
      <Route path="/editor/:name" render={() => <EditorPageContainer />} />
      <Route path="/" render={() => <Redirect to="load" />} exact />
      <Route path="/load" render={() => <InitialPageContainer />} />
      <Route path="/new" render={() => <InitialPageContainer />} />
    </div>
  );
};

export default App;
