import React, { Component } from "react";
import { connect } from "react-redux";
import Field from "./Field";
import { drawColor, eraseColor } from "../../../../actions/schema-action";
import { playSound } from "../../../../utils";

class FieldContainer extends Component {
  constructor(props) {
    super(props);
    this.handleDrawColor = this.handleDrawColor.bind(this);
  }

  shouldComponentUpdate(nextProps) {
    if (!!nextProps.scroll || !!this.props.scroll) {
      return false;
    }
    return true;
  }

  handleDrawColor(e) {
    const { drawColor, currentColor, eraseColor } = this.props;
    const target = e.target.closest("div[data-cell]");
    if (!target || !currentColor || currentColor === "#") return;
    const number = target.getAttribute("data-cell");
    if (currentColor === "#FFF") {
      playSound();
      eraseColor(number);
    } else {
      playSound();
      drawColor(number);
    }
  }

  render() {
    const { cells, width, height } = this.props;
    return (
      <Field
        cells={cells}
        width={width}
        height={height}
        handleDrawColor={this.handleDrawColor}
      />
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currentColor: state.schema.currentColor,
    cells: state.schema.cells,
    width: state.schema.width,
    height: state.schema.height,
  };
};

const mapDispatchToProps = {
  drawColor,
  eraseColor,
};

export default connect(mapStateToProps, mapDispatchToProps)(FieldContainer);
