import React from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { withRouter } from "react-router-dom";
import Controls from "./Controls";

import {
  saveSchema,
  checkCurrentColor,
  clearParams,
} from "../../../actions/schema-action";

const ControlsContainer = ({
  cells,
  name,
  width,
  height,
  colors,
  saveSchema,
  currentColor,
  checkCurrentColor,
  clearParams,
}) => {
  const handleSave = () => {
    saveSchema({ cells, name, width, height, colors });
  };

  const handleCheckCurrentColor = (e) => {
    const target = e.target.closest("div[data-color]");
    if (!target) return;
    const color = target.getAttribute("data-color");
    checkCurrentColor(color);
  };

  const handleClearState = () => {
    clearParams();
  };

  return (
    <Controls
      handleSave={handleSave}
      colorAssets={colors}
      currentColor={currentColor}
      handleCheckCurrentColor={handleCheckCurrentColor}
      handleClearState={handleClearState}
    />
  );
};

const mapStateToProps = (state, ownProps) => {
  return {
    cells: state.schema.cells,
    name: state.schema.name,
    width: state.schema.width,
    height: state.schema.height,
    colors: state.schema.colors,
    currentColor: state.schema.currentColor,
    history: ownProps.history,
  };
};

const mapDispatchToProps = { saveSchema, checkCurrentColor, clearParams };

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(ControlsContainer);
