import React, { useEffect } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { withRouter } from "react-router-dom";

import { loadSchema } from "../../actions/schema-action";

import EditorPage from "./EditorPage";

const EditorPageContainer = ({ loadSchema, name, width, height }) => {
  useEffect(() => {
    loadSchema(name);
  }, [name, loadSchema]);
  return !!width && !!height && <EditorPage />;
};

const mapStateToProps = (state, ownProps) => {
  return {
    name: ownProps.match.params.name,
    width: state.schema.width,
    height: state.schema.height,
  };
};

const mapDispatchToProps = { loadSchema };

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(EditorPageContainer);
