import React from "react";
import { connect } from "react-redux";
import FieldWrapper from "./FieldWrapper";

import { checkCurrentColor } from "../../../actions/schema-action";

const FieldWrapperContainer = ({ checkCurrentColor }) => {
  const handleCheckColor = (color) => {
    checkCurrentColor(color);
  };
  return <FieldWrapper handleCheckColor={handleCheckColor} />;
};

const mapDispatchToProps = {
  checkCurrentColor,
};

export default connect(null, mapDispatchToProps)(FieldWrapperContainer);
