import {
  GET_ALL_SCHEMAS,
  SELECT_POSITION,
  SAVE_SCHEMA,
  LOAD_SCHEMA,
  RENAME_SCHEMA,
  DELETE_SCHEMA,
  CLEAR_PARAMS,
} from "../actionTypes";

const initialState = {
  allSchemas: [],
  selectedSchema: null,
  newSchemaSaved: null,
};

const serviceReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case GET_ALL_SCHEMAS:
      return {
        ...state,
        allSchemas: payload,
      };
    case SELECT_POSITION:
      return {
        ...state,
        selectedSchema: payload,
      };
    case SAVE_SCHEMA:
      return {
        ...state,
        newSchemaSaved: payload,
        selectedSchema: null,
      };
    case LOAD_SCHEMA:
      return {
        ...state,
        newSchemaSaved: null,
        selectedSchema: null,
      };
    case RENAME_SCHEMA:
      return {
        ...state,
        allSchemas: payload,
      };
    case DELETE_SCHEMA:
      return {
        ...state,
        allSchemas: payload,
      };
    case CLEAR_PARAMS: {
      return {
        ...state,
        selectedSchema: null,
        newSchemaSaved: null,
      };
    }
    default:
      return state;
  }
};

export default serviceReducer;
