import React from "react";
import { Route, NavLink, Redirect } from "react-router-dom";
import css from "./InitialPage.module.css";
import LoadSchemaContainer from "./LoadSchema/LoadSchemaContainer";
import NewSchema from "./NewSchema/NewSchema";

const InitialPage = ({ saved }) => {
  return (
    <div className={css.wrapper}>
      <div className={css.content}>
        <div className={css.btns}>
          <button className={css.btn}>
            <NavLink to="/load" activeClassName={css.activeLink}>
              Load
            </NavLink>
          </button>
          <button className={css.btn}>
            <NavLink to="/new" activeClassName={css.activeLink}>
              New
            </NavLink>
          </button>
        </div>
        <Route
          path="/new"
          render={() =>
            !!saved ? <Redirect to={`/editor/${saved}`} /> : <NewSchema />
          }
          exact
        />
        <Route path="/load" render={() => <LoadSchemaContainer />} exact />
      </div>
    </div>
  );
};

export default InitialPage;
