import React from "react";

import css from "./SchemasList.module.css";
import SchemaItemContainer from "./SchemaItem/SchemaItemContainer";

const SchemasList = ({ allSchemas, rename, name, handleChange }) => {
  const schemas = allSchemas.map((schema) => (
    <SchemaItemContainer
      key={schema}
      name={schema}
      newName={name}
      rename={rename}
      handleChange={handleChange}
    />
  ));
  return <div className={css.wrapper}>{schemas}</div>;
};

export default SchemasList;
