import { connect } from "react-redux";
import SchemaItem from "./SchemaItem";
import { selectPosition } from "../../../../../actions/service-actions";

const mapStateToProps = (state) => {
  return {
    selectedSchema: state.service.selectedSchema,
  };
};

const mapDispatchToProps = (dispatch) => {
  const handleSelectPosition = (name) => {
    selectPosition(name)(dispatch);
  };
  return {
    handleSelectPosition,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SchemaItem);
