import React, { Component } from "react";
import css from "./FieldWrapper.module.css";
import FieldContainer from "./Field/FieldContainer";

class FieldWrapper extends Component {
  constructor(props) {
    super(props);

    this.state = {
      scroll: false,
    };

    this.startX = null;
    this.startY = null;

    this.prevX = null;
    this.prevY = null;

    this.resetColor = false;

    this.handleMouseDown = this.handleMouseDown.bind(this);
    this.handleMouseMove = this.handleMouseMove.bind(this);
    this.handleMouseUp = this.handleMouseUp.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
  }

  componentDidMount() {
    const width = (this.inner.scrollWidth - this.wrapper.offsetWidth) / 2;
    const height = (this.inner.scrollHeight - this.wrapper.offsetHeight) / 2;
    this.wrapper.scroll(width, height);
  }

  componentWillUnmount() {
    this.wrapper.scroll(0, 0);
  }

  handleMouseDown(e) {
    this.startX = e.clientX;
    this.startY = e.clientY;
    document.body.addEventListener("mousemove", this.handleMouseMove, true);
    document.body.addEventListener("mouseup", this.handleMouseUp, true);
    this.setState({
      scroll: true,
    });
  }

  handleMouseUp(e) {
    this.resetColor = false;
    this.setState({
      scroll: false,
    });
    document.body.removeEventListener("mousemove", this.handleMouseMove, true);
    document.body.removeEventListener("mouseup", this.handleMouseUp, true);
    this.prevX = 0;
    this.prevY = 0;

    this.wrapper.style.cursor = "pointer";
  }

  handleMouseMove(e) {
    const curX = e.clientX;
    const curY = e.clientY;

    if (
      Math.max(Math.abs(curX - this.startX), Math.abs(curY - this.startY)) <= 2
    ) {
      return;
    }

    this.handleScroll(curX, curY);

    this.prevX = curX;
    this.prevY = curY;
  }

  handleScroll(curX, curY) {
    if (!this.prevX || !this.prevY) return;
    const { handleCheckColor } = this.props;
    if (!this.resetColor) handleCheckColor(""); //   
    this.resetColor = true;
    this.wrapper.style.cursor = "grabbing";
    const moveX = this.prevX - curX;
    const moveY = this.prevY - curY;
    this.wrapper.scrollBy(moveX, moveY);
  }

  render() {
    const { scroll } = this.state;
    return (
      <div
        onMouseDown={this.handleMouseDown}
        onClick={this.handleClick}
        className={css.wrapper}
        ref={(wrap) => (this.wrapper = wrap)}
      >
        <div className={css.inner} ref={(inner) => (this.inner = inner)}>
          <FieldContainer scroll={scroll} />
        </div>
      </div>
    );
  }
}

export default FieldWrapper;
