import {
  SET_INITIAL_PARAMS,
  DRAW_COLOR,
  ERASE_COLOR,
  SAVE_SCHEMA,
  LOAD_SCHEMA,
  RENAME_SCHEMA,
  DELETE_SCHEMA,
  CHECK_CURRENT_COLOR,
  CLEAR_PARAMS,
} from "../actionTypes";

export const setInitialParams = (params) => (dispatch) => {
  dispatch({
    type: SET_INITIAL_PARAMS,
    payload: params,
  });
};

export const drawColor = (params) => (dispatch) => {
  dispatch({
    type: DRAW_COLOR,
    payload: params,
  });
};

export const eraseColor = (params) => (dispatch) => {
  dispatch({
    type: ERASE_COLOR,
    payload: params,
  });
};

export const saveSchema = (schema) => (dispatch) => {
  const allSchemas = JSON.parse(localStorage.getItem("schemas")) || {};
  allSchemas[schema.name] = schema;
  localStorage.setItem("schemas", JSON.stringify(allSchemas));
  dispatch({
    type: SAVE_SCHEMA,
    payload: schema.name,
  });
};

export const loadSchema = (name) => (dispatch) => {
  const allSchemas = JSON.parse(localStorage.getItem("schemas")) || {};
  dispatch({
    type: LOAD_SCHEMA,
    payload: allSchemas[name],
  });
};

export const renameSchema = (name, newName) => (dispatch) => {
  const allSchemas = JSON.parse(localStorage.getItem("schemas")) || {};
  const schema = {
    ...allSchemas[name],
    cells: { ...allSchemas[name].cells },
    colors: [...allSchemas[name].colors],
    name: newName,
  };
  allSchemas[newName] = schema;
  delete allSchemas[name];

  localStorage.setItem("schemas", JSON.stringify(allSchemas));

  dispatch({
    type: RENAME_SCHEMA,
    payload: Object.keys(allSchemas),
  });
};

export const deleteSchema = (name) => (dispatch) => {
  const allSchemas = JSON.parse(localStorage.getItem("schemas")) || {};
  delete allSchemas[name];
  localStorage.setItem("schemas", JSON.stringify(allSchemas));
  dispatch({
    type: DELETE_SCHEMA,
    payload: Object.keys(allSchemas),
  });
};

export const checkCurrentColor = (color) => (dispatch) => {
  dispatch({
    type: CHECK_CURRENT_COLOR,
    payload: color,
  });
};

export const clearParams = () => (dispatch) => {
  dispatch({
    type: CLEAR_PARAMS,
  });
};
