import React from "react";
import css from "./SchemaParamsForm.module.css";
import TextInput from "../../../../common/TextInput/TextInput";
import ColorAssets from "./ColorAssets/ColorAssets";
import Button from "../../../../common/Button/Button";
import AllColors from "./AllColors/AllColors";

const SchemaParamsForm = ({
  colors,
  setColor,
  colorsAsset,
  removeColor,
  name,
  width,
  height,
  onHandleChange,
  handleSubmit,
}) => {
  return (
    <form onSubmit={handleSubmit}>
      <div className={css.content}>
        <div className={css.inputs}>
          <label className={css.label}>
            <div className={css.title}>
              <div>Name:</div>
              <div className={css.note}>{name.note}</div>
            </div>
            <TextInput
              placeholder="A...z"
              value={name.value}
              onChange={(e) => onHandleChange("name", e)}
            />
          </label>
          <label className={css.label}>
            <div className={css.title}>
              <div>Width:</div>
              <div className={css.note}>{width.note}</div>
            </div>
            <TextInput
              placeholder="min: 100 max: 200"
              value={width.value}
              onChange={(e) => onHandleChange("width", e)}
            />
          </label>
          <label className={css.label}>
            <div className={css.title}>
              <div>Height:</div>
              <div className={css.note}>{height.note}</div>
            </div>
            <TextInput
              placeholder="min: 50 max: 100"
              value={height.value}
              onChange={(e) => onHandleChange("height", e)}
            />
          </label>
        </div>
        <div className={css.assets}>
          <ColorAssets colorsAsset={colorsAsset} removeColor={removeColor} />
        </div>
        <div className={css.buttons}>
          <Button className={css.button}>Create</Button>
        </div>
      </div>
      <AllColors colorsArr={colors} setColor={setColor} />
    </form>
  );
};

export default SchemaParamsForm;
