import React from "react";
import css from "./SchemaItem.module.css";
import CorrectName from "./CorrectName/CorrectName";

const SchemaItem = ({
  name,
  handleSelectPosition,
  selectedSchema,
  newName,
  rename,
  handleChange,
}) => {
  return (
    <div
      className={[css.wrapper, selectedSchema === name ? css.active : ""].join(
        " "
      )}
      onClick={() => !rename && handleSelectPosition(name)}
    >
      <div>
        {!!rename && name === selectedSchema ? (
          <CorrectName newName={newName} handleChange={handleChange} />
        ) : (
          name
        )}
      </div>
    </div>
  );
};

export default SchemaItem;
