import React from "react";
import css from "./Field.module.css";

const Field = React.memo(({ handleDrawColor, cells, width, height }) => {
  const drawField = () => {
    const arr = [];
    let counter = 1;

    for (let i = 1; i <= height; i++) {
      const row = [];
      for (let j = 1; j <= width; j++) {
        row.push(counter);
        counter++;
      }
      arr.push(row);
    }

    const table = arr.map((row, index) => {
      const rowArr = row.map((cell, index) => {
        return (
          <div
            key={cell}
            className={[css.cell, index % 5 === 0 ? css.borderedCell : ""].join(
              " "
            )}
            data-cell={cell}
            style={{ backgroundColor: "#" + !!cells[cell] && cells[cell] }}
          />
        );
      });
      return (
        <div className={index % 5 === 0 ? css.borderedRow : ""} key={index}>
          {rowArr}
        </div>
      );
    });

    return table;
  };

  const field = drawField();

  return (
    <div className={css.wrapper} onClick={handleDrawColor}>
      <div className={css.inner}>{field}</div>
    </div>
  );
});

export default Field;
