const Controls = ({
  handleSave,
  colorAssets,
  currentColor,
  handleCheckCurrentColor,
  handleClearState,
}) => {
  return (
    <div className={css.wrapper}>
      <ColorsSelect
        colors={colorAssets}
        currentColor={currentColor}
        handleCheckCurrentColor={handleCheckCurrentColor}
      />
      <div className={css.buttons}>
        <ButtonPrimary onClick={handleSave}>Save</ButtonPrimary>
        <div>
          <Link to="/">
            <ButtonPrimary onClick={handleClearState}>Menu</ButtonPrimary>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default Controls;
