import React from "react";
import css from "./AllColors.module.css";

const AllColors = ({ colorsArr, setColor }) => {
  const colors = colorsArr.map((color, index) => {
    return (
      <div
        key={index}
        data-color={color}
        className={css.color}
        style={{ backgroundColor: `#${color}` }}
      />
    );
  });
  return (
    <div className={css.wrapper} onClick={setColor}>
      {colors}
    </div>
  );
};

export default AllColors;
